<script setup lang="ts">
import useAsync from "~/composables/use-async";
import CategoriesAPI from "~/services/categories";

const { t } = useI18n();
const route = useRoute();
const leagueId = route.params.leagueId as string;

const onError = () => showError({ statusCode: 404 });
const { data: league, loading: loadingLeague, run: loadLeague } = useAsync(CategoriesAPI.loadById, { onError });

const breadcrumbs = computed(() => [
  { to: "/following", title: t("labels.following") },
  { to: `/following/league/${leagueId}/followed`, title: loadingLeague ? t("labels.loading") : league?.value?.title ?? "", active: true },
]);

onMounted(() => {
  loadLeague(leagueId);
});

useHead({ title: computed(() => league.value?.title) as MaybeRef<string> });
definePageMeta({ layout: "home", middleware: "auth" });
</script>

<template>
  <div class="main-container">
    <BaseBreadcrumbs class="mb-10" :items="breadcrumbs" />

    <ListTeams
      title="Teams"
      has-load-more-button
      load-only-followed
      :league-id="leagueId"
      link-list
    >
      <template #start>
        <div class="mt-10 mb-6 text-2xl font-bold text-main">
          {{ loadingLeague ? t("labels.loading") : league?.title }}
        </div>
      </template>
    </ListTeams>
  </div>
</template>
